"use client";

import { useState, type ElementType, useCallback } from 'react';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import { Beaker, CheckCircle, Landmark, Lock, Sigma } from 'lucide-react';
import { useUnlockStore, type Subject } from '@/hooks/use-unlock-store';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogFooter } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { useToast } from '@/hooks/use-toast';
import { Skeleton } from './ui/skeleton';
import { Loader2 } from 'lucide-react';
import { cn } from '@/lib/utils';


type SubjectInfo = {
  id: Subject;
  name: string;
  icon: ElementType;
  color: string;
  glowColor: string;
};

const subjects: SubjectInfo[] = [
  { id: 'science', name: 'Science', icon: Beaker, color: 'hover:border-emerald-500/50 hover:shadow-emerald-500/20', glowColor: 'shadow-emerald-500/50 border-emerald-500/50' },
  { id: 'math', name: 'Math', icon: Sigma, color: 'hover:border-sky-500/50 hover:shadow-sky-500/20', glowColor: 'shadow-sky-500/50 border-sky-500/50' },
  { id: 'social-studies', name: 'Social Studies', icon: Landmark, color: 'hover:border-amber-500/50 hover:shadow-amber-500/20', glowColor: 'shadow-amber-500/50 border-amber-500/50' },
];

function UnlockDialog({ subject, isOpen, onOpenChange, onUnlockSuccess }: { subject: SubjectInfo, isOpen: boolean, onOpenChange: (open: boolean) => void, onUnlockSuccess: () => void }) {
  const [code, setCode] = useState('');
  const [error, setError] = useState('');
  const [isUnlocking, setIsUnlocking] = useState(false);
  const { unlockSubject } = useUnlockStore();
  const { toast } = useToast();

  const handleUnlock = async () => {
    setError('');
    setIsUnlocking(true);
    const success = await unlockSubject(subject.id, code);
    setIsUnlocking(false);

    if (success) {
      onUnlockSuccess();
      onOpenChange(false);
      setCode('');
      toast({
        title: "Success!",
        description: `You've unlocked the content.`,
      });
    } else {
      setError('Invalid or already used code. Please try again.');
    }
  };

  return (
    <Dialog open={isOpen} onOpenChange={onOpenChange}>
      <DialogContent>
        <DialogHeader>
          <DialogTitle>Unlock {subject.name}</DialogTitle>
          <DialogDescription>Enter your one-time code to view the study materials for this subject.</DialogDescription>
        </DialogHeader>
        <div className="space-y-2">
          <Input 
            placeholder="Enter unlock code"
            value={code}
            onChange={(e) => setCode(e.target.value)} 
            onKeyDown={(e) => e.key === 'Enter' && !isUnlocking && handleUnlock()}
            disabled={isUnlocking}
          />
          {error && <p className="text-sm text-destructive">{error}</p>}
        </div>
        <DialogFooter>
          <Button onClick={handleUnlock} disabled={isUnlocking}>
            {isUnlocking && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
            Unlock
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}


function SubjectCard({ subject, isUnlocked, onUnlockSuccess }: { subject: SubjectInfo, isUnlocked: boolean, onUnlockSuccess: () => void }) {
  const [isDialogOpen, setDialogOpen] = useState(false);

  const CardContentWrapper = ({ children }: { children: React.ReactNode }) => {
    const commonProps = {
      className: cn(
        "w-full h-full p-0 transition-all duration-300 ease-in-out group",
        !isUnlocked && subject.color,
        isUnlocked && subject.glowColor,
        isUnlocked && "shadow-lg"
      )
    };

    if (isUnlocked) {
      return (
        <Link href={`/subject/${subject.id}`} className="block w-full h-full">
            <Card {...commonProps}>{children}</Card>
        </Link>
      );
    }
    return (
      <Card {...commonProps} onClick={() => setDialogOpen(true)} role="button" tabIndex={0}>
        {children}
      </Card>
    );
  };
  
  return (
    <>
      <CardContentWrapper>
        <div className="flex flex-col items-center justify-center text-center h-full p-6">
          <div className="relative mb-4">
            <subject.icon className="h-16 w-16 text-primary transition-transform duration-300 group-hover:scale-110" />
            <div className="absolute -top-1 -right-1 bg-background rounded-full p-1 shadow-md">
              {isUnlocked ? <CheckCircle className="h-6 w-6 text-emerald-500" /> : <Lock className="h-6 w-6 text-muted-foreground" />}
            </div>
          </div>
          <CardTitle className="font-headline text-2xl">{subject.name}</CardTitle>
          <p className="text-muted-foreground mt-2">{isUnlocked ? 'Content Unlocked' : 'Locked'}</p>
        </div>
      </CardContentWrapper>
      {!isUnlocked && <UnlockDialog subject={subject} isOpen={isDialogOpen} onOpenChange={setDialogOpen} onUnlockSuccess={onUnlockSuccess} />}
    </>
  );
}

function CardSkeleton() {
    return (
        <Card className="w-full h-full p-6">
            <div className="flex flex-col items-center justify-center text-center h-full">
                <Skeleton className="h-16 w-16 rounded-full mb-4" />
                <Skeleton className="h-8 w-32 mb-2" />
                <Skeleton className="h-4 w-24" />
            </div>
        </Card>
    );
}

export default function SubjectSelection() {
  const { isUnlocked, isInitialized } = useUnlockStore();
  // We use a local state to trigger re-renders, which is more reliable than router.refresh() for client-side state changes.
  const [unlockedStatus, setUnlockedStatus] = useState<Record<Subject, boolean>>({
    'science': false,
    'math': false,
    'social-studies': false,
  });

  const handleUnlockSuccess = useCallback(() => {
    // By creating a new object, we ensure React detects the state change and re-renders.
    setUnlockedStatus(prev => ({...prev})); 
  }, []);

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8 w-full max-w-5xl">
      {subjects.map((subject) => (
        <div key={subject.id} className="h-64">
          {!isInitialized ? (
            <CardSkeleton />
          ) : (
            <SubjectCard 
              subject={subject} 
              isUnlocked={isUnlocked(subject.id)}
              onUnlockSuccess={handleUnlockSuccess}
            />
          )}
        </div>
      ))}
    </div>
  );
}
